import Vue from 'vue'
import Vuex from 'vuex'
import usersAPI from './../apis/users'

Vue.use(Vuex)

export default new Vuex.Store({
  // 資料
  state: {
    currentUser: {
      id: -1,
      name: '',
      account: '',
      email: '',
      avatar: '',
      cover: '',
      introduction: '',
      role: ''
    },
    isAuthenticated: false,
    token: '',
    recommendation: []
  },
  // 資料操作
  mutations: {
    // 登入
    setCurrentUser(state, currentUser) {
      state.currentUser = {
        ...state.currentUser,
        ...currentUser
      }
      state.token = localStorage.getItem('token')
      state.isAuthenticated = true
    },
    // 登出
    revokeAuthentication(state) {
      state.currentUser = {}
      state.isAuthenticated = false
      localStorage.removeItem('token')
      localStorage.removeItem('userId')
      state.token = ''
    },
    // 存入推薦使用者
    setRecommendation(state, recommendation) {
      state.recommendation = [...recommendation]
    },
    // 追蹤推薦使用者
    followRecommendation(state, userId) {
      state.recommendation = state.recommendation.map(user => {
        if (user.id === userId) {
          return {
            ...user,
            followerCount: user.followerCount ++,
            isFollowed: true
          }
        }
        return user
      })
    },
    // 取消追蹤推薦使用者
    unfollowRecommendation(state, userId) {
      state.recommendation = state.recommendation.map(user => {
        if (user.id === userId) {
          return {
            ...user,
            followerCount: user.followerCount --,
            isFollowed: false
          }
        }
        return user
      })
    }
  },
  // 非同步操作
  actions: {
    // 確認使用者登入狀態
    async fetchCurrentUser({ commit }) {
      try {
        console.log('setCurrentUser')
        const { data } = await usersAPI.getCurrentUser()
        if (data.status === 'error') {
          throw new Error(data.message)
        }
        const { id, name, account, email, avatar, role, cover, introduction } = data
        commit('setCurrentUser', {
          id: id || -1,
          name: name || '',
          account: account || '',
          email: email || '',
          avatar: avatar || '',
          role: role || '',
          cover: cover || '',
          introduction: introduction || ''
        })
        return true
      } catch(err) {
        console.log(err)
        commit('revokeAuthentication')
        return false
      }
    }
  },
  modules: {
  }
})
